/*
* -- Grid Mixins
*/
*, *::before, *::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
}

h1, h2, h3, h4, h5, h6 {
  font-size: normal;
}

img, video, embed, object {
  max-width: 100%;
}

img {
  vertical-align: middle;
}

label, button {
  cursor: pointer;
}

.ma {
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.site-width {
  max-width: 75em;
}

.clearfix::before, .clearfix::after, .grid::before, .grid::after {
  content: "";
  display: table;
}

.clearfix::after, .grid::after {
  clear: both;
}

.align-center {
  text-align: center;
}

html, body {
  background: #fff;
  color: #000;
}

body, input, button, select, textarea {
  font-family: 'Open Sans', sans-serif;
  font-size: 100%;
}

/*
* -- Header
*/
.page-nav {
  background-color: #15263e;
  color: #fff;
  text-align: center;
  padding: 1em 0 0;
}

@media (min-width: 48em) {
  .page-nav {
    padding: 1.5em;
  }
}

.page-nav ul {
  list-style: none;
}

.page-nav__li {
  position: relative;
  border-bottom: 1px solid #ccc;
}

@media (min-width: 48em) {
  .page-nav__li {
    display: inline-block;
    border: 0;
  }
  .page-nav__li::after {
    content: "-";
    position: absolute;
    top: 50%;
    right: -0.15em;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
  }
  .page-nav__li:last-child::after {
    display: none;
  }
}

.page-nav__item {
  display: block;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
  padding: 0.5em 1em;
}

.page-nav__item:hover {
  opacity: 0.7;
}

@media (min-width: 48em) {
  .page-nav__item {
    font-size: 1.125em;
    padding: 1em 2em;
  }
}

.page-nav__hamburger {
  display: inline-block;
  cursor: pointer;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  margin-bottom: 1em;
  font-size: 2em;
}

.page-nav__hamburger:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

@media (min-width: 48em) {
  .page-nav__hamburger {
    display: none;
  }
}

.page-nav__ul {
  display: none;
}

@media (min-width: 48em) {
  .page-nav__ul {
    display: block !important;
  }
}

/*
* -- General sections
*/
.section {
  /*margin-bottom: 3em;*/
  padding-top: 3em;
}

.section__title {
  text-transform: uppercase;
  color: #15263e;
  font-weight: bold;
  text-align: center;
  margin-bottom: 0.5em;
  font-size: 2em;
}

@media (min-width: 48em) {
  .section__title {
    font-size: 2.625em;
  }
}

@media (min-width: 75em) {
  .section__title {
    font-size: 3.125em;
  }
}

.section__intro-text {
  color: #4d4d4d;
  text-align: center;
  font-size: 1.125em;
  margin-bottom: 1.5rem;
}

@media (min-width: 48em) {
  .section__intro-text {
    margin-bottom: 3rem;
    font-size: 1em;
  }
}

@media (min-width: 75em) {
  .section__intro-text {
    margin-bottom: 4rem;
    font-size: 1.125em;
  }
}

.welcome {
  background-color: #ccc;
}

.services, .blog {
  background-color: #e7fff7;
}

/*
* -- Footer
*/
.page-footer {
  background: #dcdcde;
  padding: 6.25em 1.5em;
}

.footer-module {
  margin-bottom: 2em;
  font-size: 0.875em;
}

.footer-module:last-child {
  margin-bottom: 0;
}

.footer-module__icon, .footer-module__content {
  display: inline-block;
  vertical-align: middle;
}

@media (min-width: 48em) {
  .footer-module__icon, .footer-module__content {
    vertical-align: top;
  }
}

.footer-module__icon {
  background-color: #374362;
  width: 5.4375rem;
  height: 5.4375rem;
  border-radius: 100%;
  position: relative;
  font-size: 2.8125em;
  color: #fff;
}

.footer-module__icon .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.footer-module__title {
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 1em;
  font-size: 1em;
}

.footer-module__content {
  color: #4d4d4d;
  margin-left: 2em;
}

.footer-module__content a {
  color: inherit;
  text-decoration: none;
}

.footer-module__content a:hover {
  text-decoration: underline;
}

