/*
* -- Welcome section
*/
.welcome {
  background: url(../images/welcome_background.jpg) no-repeat center center;
  background-size: cover;
  position: relative;
  height: 12.5em;
}

@media (min-width: 48em) {
  .welcome {
    height: 21.25em;
  }
}

@media (min-width: 75em) {
  .welcome {
    height: 37.5em;
  }
}

@media (min-width: 100em) {
  .welcome {
    height: 40em;
  }
}

@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 2 / 1), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (max-resolution: 2dppx) {
  .welcome {
    background-image: url(../images/welcome_background@2x.jpg);
  }
}

.welcome__title {
  font-size: 3rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 300;
  color: #FFF;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.75);
  line-height: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@media (min-width: 48em) {
  .welcome__title {
    font-size: 4.5rem;
  }
}

@media (min-width: 75em) {
  .welcome__title {
    font-size: 7.375rem;
  }
}

.welcome__title-complement {
  font-size: 0.875rem;
  display: block;
  font-weight: 600;
}

@media (min-width: 48em) {
  .welcome__title-complement {
    font-size: 1.5rem;
  }
}

@media (min-width: 75em) {
  .welcome__title-complement {
    font-size: 2.25rem;
  }
}

/*
* -- Services section
*/
.services__list {
  list-style: none;
}

.services__item {
  text-align: center;
  margin-bottom: 4em;
  font-size: 0.875em;
}

@media (min-width: 48em) {
  .services__item {
    margin-bottom: 3em;
    font-size: 1em;
  }
}

@media (min-width: 75em) {
  .services__item {
    margin-bottom: 4em;
    font-size: 1.125em;
  }
}

.services__item-icon {
  font-size: 2.8125em;
  display: inline-block;
  width: 7.1875rem;
  height: 7.1875rem;
  background: linear-gradient(45deg, #b119ec, #f8027e);
  border-radius: 100%;
  margin-bottom: 1.3rem;
  position: relative;
  color: #fff;
}

.services__item-icon .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.services__item-title {
  font-size: 1.5625em;
  font-weight: bold;
  margin-bottom: 1.3rem;
  text-transform: uppercase;
  color: #4d4d4d;
}

.services__item-description {
  color: #494949;
  line-height: 1.9;
}

/*
* -- Portfolio
*/
.portfolio__nav, .portfolio__list {
  list-style-type: none;
}

.portfolio__nav {
  text-align: center;
  margin-bottom: 2em;
}

@media (min-width: 48em) {
  .portfolio__nav {
    text-align: left;
  }
}

.portfolio__nav-li {
  display: inline-block;
}

.portfolio__nav-li.is-active .portfolio__nav-item {
  color: #a91aef;
}

@media (min-width: 48em) {
  .portfolio__nav-li {
    display: block;
  }
}

.portfolio__nav-item {
  color: #4d4d4d;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
  display: inline-block;
  font-size: 1em;
  margin: 0.3em 0.8em;
}

.portfolio__nav-item:hover {
  color: #a91aef;
}

@media (min-width: 48em) {
  .portfolio__nav-item {
    display: inline-block;
    font-size: 1.375em;
    margin: 0 0 1.25em;
  }
}

.hide {
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
  overflow: hidden;
  z-index: -1;
  opacity: 0;
}

.tab-cont {
  opacity: 1;
  transition: opacity 0.4s;
}

.tab-cont.hide {
  opacity: 0;
}

.portfolio__item {
  display: block;
  margin-bottom: 2em;
  text-decoration: none;
  border: 3px solid transparent;
  -webkit-transition: border 0.2s ease;
  transition: border 0.2s ease;
}

.portfolio__item:hover {
  border-color: #a91aef;
}

.portfolio__item img {
  max-width: none;
  width: 100%;
}

/*
* -- Blog
*/
.blog__list {
  list-style-type: none;
}

.blog__item {
  font-size: 0.875em;
  margin-bottom: 2em;
}

@media (min-width: 48em) {
  .blog__item {
    font-size: 1em;
    margin-bottom: 3em;
  }
}

@media (min-width: 75em) {
  .blog__item {
    font-size: 1.125em;
    margin-bottom: 4em;
  }
}

.blog__item-image-container {
  margin-bottom: 1.5em;
}

.blog__item-image-container > a {
  display: inline-block;
  border: 3px solid transparent;
  -webkit-transition: border 0.2s ease;
  transition: border 0.2s ease;
}

.blog__item-image-container > a:hover {
  border-color: #a91aef;
}

.blog__item-image-container img {
  max-width: none;
  width: 100%;
  border: 1px solid transparent;
}

.blog__item-header, .blog__item-excerpt {
  padding: 0 3px;
}

.blog__item-tag {
  display: inline-block;
  text-transform: uppercase;
  font-weight: 600;
  color: #929292;
  margin-bottom: 0.5em;
  text-decoration: none;
}

.blog__item-tag:hover {
  text-decoration: underline;
}

.blog__item-title {
  font-weight: bold;
  color: #color-base;
  font-size: 1.25em;
  margin-bottom: 1rem;
}

.blog__item-title a {
  color: inherit;
  text-decoration: none;
}

.blog__item-title a:hover {
  text-decoration: underline;
}

.blog__item-excerpt {
  color: #494949;
  line-height: 1.9;
}

.blog__item-excerpt p {
  margin-bottom: 1.5em;
}

.blog__item-read {
  display: block;
  text-align: right;
  color: #a91aef;
  font-weight: bold;
  text-decoration: none;
}

.blog__item-read:hover {
  text-decoration: underline;
}

.blog__item-read::after {
  content: " \00bb";
}

/*
* -- Contact
*/
.contact {
  background-color: #15263e;
  margin-bottom: 0;
  padding-bottom: 1rem;
}

.contact .section__title, .contact .section__intro-text {
  color: #fff;
}

.contact__wrapper {
  max-width: 61.125em;
  padding-bottom: 3em;
}

.contact-form {
  font-size: 0.875em;
}

@media (min-width: 48em) {
  .contact-form {
    font-size: 1em;
  }
}

@media (min-width: 75em) {
  .contact-form {
    font-size: 1.125em;
  }
}

.contact-form__container {
  margin-bottom: 0.75em;
}

.contact-form__label {
  display: block;
  color: #fff;
  margin-bottom: 0.2em;
}

.contact-form__field {
  display: block;
  padding: 0.5em;
  color: #4d4d4d;
  border: 0;
  background-color: #fff;
  width: 100%;
  -webkit-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
  border-radius: 0;
  -webkit-appearance: none;
}

.contact-form__field:focus {
  background-color: #ffc;
}

.contact-form__field--message {
  height: 8em;
  margin-bottom: 2em;
}

.contact-form__button {
  padding: 1em 2em;
  text-transform: uppercase;
  font-size: 1.125em;
  color: #fff;
  font-weight: bold;
  border: 0;
  background: -webkit-gradient(linear, left top, right top, from(#b119ec), to(#f8027e));
  background: linear-gradient(90deg, #b119ec, #f8027e);
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.contact-form__button:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
